import React from 'react';
import { NavLink } from 'react-router-dom';

const Navbar = () =>{
    return (
        <>
        <div className='nav bg'>
            <div className="row">
                <div className="col-12 mx-auto">
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid">
    <a className="navbar-brand" href="/">TechClass</a>
    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink activeClassName="nav_active" exact className="nav-link active" aria-current="page" to="/">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="nav_active" exact className="nav-link" to="/service">Services</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="nav_active" exact className="nav-link" to="/about">About</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="nav_active" exact className="nav-link" to="/contact">Contact Us</NavLink>
        </li>
        </ul>
        </div>
  </div>
</nav>
</div>
            </div>
        </div>
        </>
    );
}

export default Navbar;